button {
    background-color: white;
    padding: 5px 15px;
    border-radius: 20px;
    border-width: 0px;
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.454);
    /* margin: 3px; */
    /* width: 70px;
    height: 70px; */
    color: black;
    font-size: 16px;
    font-weight: bold;
    transition-property: background-color, border-color;
    /* transition-duration:  0.2s; */
    /* transition: background-color, border-color 0.5s ease-in-out; */
}

button:hover {
    background-color: #c1c1c1;
    cursor: pointer;
}

.active:hover {
    background-color: black;
    cursor: pointer;
}

.active {
    background-color: black;
    color: white;
}

label {
    font-weight: bold;
    margin-right: 10px;
    text-align: center;
    margin-bottom: 10px;
    color: white;
    text-shadow: 1px 1px 8px rgba(0,0,0,0.35);
    margin-top: 10px;
}